/**
 * File scrolled-down-page.js.
 * 
 * Applique une classe au body après un scroll
 * 
 * Peut être appliqué de multiple fois avec des classes différentes
 * 
 * @param string c  La classe
 * @param int p		Le break point auquel la classe apparait
 * @returns null
 */
function toggleClassOnScroll( c, p){
	jQuery(window).scroll(function(){
		jQuery('body').toggleClass( c , jQuery(window).scrollTop() > p );
	});
}

toggleClassOnScroll('scrolled',0);